@charset "UTF-8";
.text-section {
  color: hsl(300, 43%, 22%);
}

.rating-section article {
  background-color: hsl(300, 24%, 96%);
}

.members-section article {
  background-color: hsl(300, 43%, 22%);
}
.members-section h2 {
  color: hsl(0, 0%, 100%);
  font-size: 1rem;
  font-weight: 700;
  margin-bottom: 3px;
}
.members-section h3 {
  color: hsl(333, 80%, 67%);
  font-size: 1rem;
  margin-bottom: 2rem;
  font-weight: 500;
}
.members-section p {
  color: hsl(300, 24%, 96%);
  line-height: 1.3;
}

*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  position: relative;
  display: grid;
  place-items: center;
  padding: 5rem 1.5rem;
  background: url(../../../images/bg-pattern-top-mobile.svg) no-repeat left top, url(../../../images/bg-pattern-bottom-mobile.svg) no-repeat right bottom;
}
@media (min-width: 800px) {
  body {
    background: url(../../../images/bg-pattern-top-desktop.svg) no-repeat left top, url(../../../images/bg-pattern-bottom-desktop.svg) no-repeat right bottom;
  }
}
body footer {
  position: absolute;
  bottom: 0;
}

@media (min-width: 800px) {
  main {
    display: grid;
    grid-template-areas: "tx . rt" ". . ." "cs cs cs";
  }
  main .txt-section {
    grid-area: tx;
    text-align: left;
    padding: 3rem 0rem;
    margin-top: -7rem;
    margin-bottom: 5rem;
  }
  main .txt-section h1 {
    color: hsl(300, 43%, 22%);
    max-width: 20rem;
    margin-left: -10rem;
    font-size: 2.8rem;
  }
  main .txt-section p {
    color: hsl(300, 43%, 22%);
    max-width: 30rem;
    text-align: left;
  }
  main .rating-section {
    grid-area: rt;
    margin-top: -5rem;
    margin-left: -4rem;
  }
  main .rating-section article {
    display: flex;
    flex-direction: row;
    gap: 2rem;
  }
  main .rating-section article h2 {
    font-size: 16px;
    color: hsl(300, 43%, 22%);
  }
  main .rating-section article:nth-child(1) {
    margin-left: -10rem;
    width: 30rem;
  }
  main .rating-section article:nth-child(2) {
    margin-left: -7rem;
    width: 31rem;
  }
  main .rating-section article:nth-child(3) {
    margin-left: -4rem;
    width: 32rem;
  }
  main .members-section {
    grid-area: cs;
    display: flex;
    align-items: center;
    column-gap: 1.7rem;
    margin-bottom: -5rem;
  }
  main .members-section article:nth-child(1) {
    margin-top: -6rem;
  }
  main .members-section article:nth-child(2) {
    margin-top: -3rem;
  }
  main .members-section article:nth-child(3) {
    margin-top: 0.5rem;
  }
}
html {
  font-family: "League Spartan", sans-serif;
  font-size: 15px;
}

.txt-section {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2.2rem;
}
.txt-section h1 {
  font-size: 2.4rem;
  width: 20.6666666667rem;
  line-height: 0.8;
  margin-bottom: 1.5rem;
}
.txt-section p {
  font-size: 1.2rem;
  line-height: 1.3;
}

.rating-section {
  text-align: center;
}
.rating-section article {
  padding: 1rem 2rem;
  margin-bottom: 1.1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 0.5rem;
}
.rating-section article .stars {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 0.7rem;
}
.rating-section article .stars li {
  list-style: none;
  height: 1.0666666667rem;
  width: 1.1333333333rem;
  background: url(../../../images/icon-star.svg);
}
.rating-section article h2 {
  font-size: 1.2rem;
  text-align: center;
}

.members-section article {
  margin-bottom: 1rem;
  border-radius: 0.5rem;
  padding: 3rem 2rem;
  max-width: 25.2133333333rem;
  max-height: 15.96rem;
}
.members-section article img {
  border-radius: 50%;
  width: 3rem;
}
.members-section article .text-user {
  text-align: center;
  margin-top: -3rem;
  margin-left: -6rem;
}
.members-section blockquote p::before {
  content: "“";
}
.members-section blockquote p::after {
  content: "”";
}

.attribution {
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: hsl(228, 45%, 44%);
}